import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    MarketList: [],
    MarketId: '',
    HomeSale: {
      saleData: {},
      saleTrend: [],
      saleClassify: [],
      customerPay: [],
    },
  },
  mutations: {
    updateHomeSale(state, options) {
      const newState = state
      const {
        saleData,
        saleTrend,
        saleClassify,
        customerPay,
      } = options
      newState.HomeSale.saleData = saleData
      newState.HomeSale.saleTrend = saleTrend
      newState.HomeSale.saleClassify = saleClassify
      newState.HomeSale.customerPay = customerPay
    },
    updateMarketId(state, options) {
      const newState = state
      const LocalMarketId = localStorage.getItem('marketId')
      if (LocalMarketId === options) {
        newState.MarketId = localStorage.setItem('marketId', options)
      } else {
        newState.MarketId = options
        localStorage.setItem('marketId', options)
      }
    },
    updateMarketList(state, options) {
      const newState = state
      newState.MarketList = options
    },
  },
  actions: {

  },
})
